    font-weight: bolder;
    margin-left: 25px;
    cursor: pointer;
  }
  .name_logo > span {
    color: yellow;
  }
  .NavbarSubDiv {
    /* border: 1px solid rgb(7, 7, 20); */
    height: 80%;
    width: 50%;
    display: flex;
    /* margin: auto; */
    /* margin-top: 6px; */
    justify-content: space-around;
    padding: 5px;
  }

  .NavbarSubDiv > div {
    box-shadow: rgba(184, 184, 184, 0.35) 0px 5px 15px;
    border: none;
    border-radius: 8px;
  }

