    margin-left: -50%;
  }
  .hey > span {
    color: red;
  }
  .socialIcon {
    width: 300px;
    /* margin: auto; */
    /* border: 1px solid red; */
    display: flex;
    gap: 20px;
    padding: 10px;

    margin-left: 70%;
    margin-top: 80px;
  }
  .linkedIn {
    content: "";
    position: static;
    top: 0;
    left: -100px;
    width: 70px;
    height: 100%;

    animation-name: slide;
    animation-duration: 3s;
    animation-timing-function: ease-in-out;
    animation-delay: 0.4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
  }
  @keyframes slide {
    0% {
      left: -100;
      top: 0;
      color: rgba(238, 5, 5, 0.938);
    }
    50% {
      left: 120px;
      top: 0px;
      color: rgba(153, 223, 228, 0.89);
    }
    100% {
      left: 60px;
      top: 0;
      color: rgb(49, 118, 247);
    }
  }
