    /* overflow-y: hidden; */
    padding-top: 50px;
    /* border: 2px solid red; */
    width: 100vw;
    /* width: 100%; */
    height: 500px;
    /* display: flex; */
    /* background-color: rgb(79, 79, 82); */
    color: goldenrod;
  }

  .left_home_div {
    width: 80%;
    height: 90%;
    margin: auto;
    margin-top: 80px;
    border: 2px solid rgba(100, 100, 100, 0.507);
    padding-top: 10px;
    backdrop-filter: blur(30px);
    border-radius: 30px 30px 0 0;

    font-size: 12px;

    animation-name: slide;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-delay: 0.4s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation: rotate 1.25s ease-in-out;
  }
  @keyframes rotate {
    0% {
      rotate: 0deg;
    }
    100% {
      /* border-radius: 50%; */
      rotate: 360deg;
    }
  }

