* {
  scroll-behavior: smooth;
}
.navbar {
  /* background-color: #1f5156; */
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  z-index: 20;
  backdrop-filter: blur(10px);
  width: 100%;
}

.nav-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 80px;
  /* max-width: 1500px; */
  /* border: 1px solid red; */
  padding: 20px;
  /* padding-right: 50px; */
}

.nav-logo {
  /* width: 140px; */
  /* border: 1px solid red; */
  color: #f5b921;
  align-items: center;
  /* margin-left: 20px; */
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  /* flex-grow: 1; */
}

.nav-menu {
  display: flex;
  list-style: none;
  text-align: center;
  margin-right: 2rem;
}

.nav-links {
  color: goldenrod;
  text-decoration: none;
  padding: 0.5rem 0.5rem;
  height: 100%;
  border-bottom: 3px solid transparent;
}
.fa-code {
  margin-left: 1rem;
}

.nav-item {
  line-height: 40px;
  margin-right: 1rem;
}

.nav-item:after {
  content: "";
  display: block;
  height: 3px;
  width: 0;
  background: transparent;
  transition: width 0.7s ease, background-color 0.5s ease;
}

.nav-item:hover:after {
  width: 100%;
  background: #dfbb1c;
}

.nav-item .active {
  color: black;
  border: 1px solid #ffdd40;
}

.nav-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    border-top: 1px solid #fff;
    position: absolute;
    top: 80px;
    left: -110%;
    opacity: 1;
    transition: all 0.5s ease;
  }

  .nav-menu.active {
    /* background: #1f5156; */
    background: rgba(32, 32, 31, 0.979);
    left: 0px;
    opacity: 1;
    transition: all 0.5s ease;
    height: 1000px;
    /* width */
    color: black;
  }
  .nav-item .active {
    color: #ffdd40;
    /* color: black; */
    border: none;
  }
  .nav-links {
    /* padding: 1.5rem; */
    width: 100%;
    display: table;
  }

  .nav-icon {
    /* border: 1px solid red; */
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    margin-top: -16px;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
    color: goldenrod;
  }
}
